@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap);
.container {
  display: grid;
  grid-column-gap: 50px;
  grid-template-columns: auto auto;
}

.logo {
  grid-column-start: 3;
  grid-column-end: 3;
  font-size: 3em;
  margin-top: 8px
  

}
.formulario {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start:1;
  grid-row-end: 1;
  font-size: 12px;
  margin: 72px ;
  box-shadow: 0.2em 0.3em 0.3em rgb(0, 0, 0);
  border: 0.1em solid #d6d6d6;
  border-radius: 1em;
}


/* @import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;400&display=swap'); */

.map-container {
  height: 100vh;
  display: flex;
}
.logoMapa {
  display: flex;
  box-shadow: 0.2em 0.3em 0.3em rgb(0, 0, 0);
  border: 0.1em solid #d6d6d6;
  border-radius: 1em;
  margin: 5px;
  justify-content: space-between;
  align-content: center;
  font-family: monospace;
  color: rgba(35, 55, 75, 0.9);
  height: 85px;
}
.sidebar {
  background-color: rgba(35, 55, 75, 0.9);
  color: #fff;
  padding: 6px 12px;
  font-family: monospace;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  margin: 12px;
  border-radius: 4px;
}
#marker {
  background-image: url("https://docs.mapbox.com/mapbox-gl-js/assets/washington-monument.jpg");
  background-size: cover;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
}

